body{
    background-color: #222733;
    font-family: 'Open Sans', sans-serif;
    font-size: 1.6rem;
    color: #DBE8D4;
    padding-bottom: 5rem;
    
}

*::selection{
    background-color: rgba(255, 255, 255, 0.1);
}

button{
    display: block;
}
img{
    max-width: 100%;
}

p, li{
    display: block;
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 200%;
    @media (max-width: 640px) {
        font-size: 1.4rem;
    }
}

span{
    font-weight: 800;
}

h3{
    display: inline-block;
    margin-top: 8rem;
    font-size: 4rem;
    font-weight: 600;
    letter-spacing: .6px;
    line-height: 120%;
    &::after{
        content: '';
        position: absolute;
        bottom: -2rem;
        left: 0;
        width: 34rem;
        height: .3rem;
        background: rgba(255, 255, 255, 0.6);
    }
    @media (max-width: 460px) {
        font-size: 3rem;
        margin-top: 4rem;
        &::after{
            width: 25rem;
        }
    }
}


.container{
    padding: 0 2rem;
    width: 100%;
    max-width: 94.4rem;
    margin: 0 auto;
}


.title{
    font-size: 2.8rem;
    font-weight: 300;
    z-index: 2;
    text-align: center;
    font-size: 3rem;
    letter-spacing: .4rem;
    @media (max-width: 640px) {
        font-size: 2.5rem;
    }
    &-big{
        text-transform: uppercase;
        font-size: 5.5rem;
        letter-spacing: .5rem;
        line-height: 110%;
        @media (max-width: 640px) {
            font-size: 1.5rem;
        }
        & + .title{
            margin-top: 2rem;
        }
    }
}


.content{
    margin-top: 5rem;
    z-index: 2;
}