@import './vars.scss';

.header{
    position: fixed;
    background-color: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(1rem);
    display: flex;
    align-items: center;
    width: 100%;
    left: 0;
    top: 0;
    box-shadow: 0 7px 20px 0 rgb(0 0 0 / 20%), 0 4px 10px 0 rgb(0 0 0 / 20%);
    z-index: 8;
    transition: all .3s linear;
    .container{
        max-width: 47rem;
    }
    &__menu{
        display: flex;
        justify-content: center;
        gap: 2rem;
        li{
            display: inline-block;
            a{
                display: flex;
                align-items: center;
                padding: .3rem 1rem;
                height: 5rem;
                transition: color, border-color .2s ease-in-out;
                border-bottom: 4px solid rgba(255, 255, 255, 0);
                color: #fff;
                @media (max-width: 360px) {
                    font-size: 1.5rem;
                }
                &:not(.active):hover{
                    border-color: $mainColorHover;
                }
                &.active{
                    color: $mainColor;
                    border-color: $mainColor;
                    &:hover{
                        opacity: 0.7;
                    }
                }
                &:active{
                    transition: all .1s linear;
                    transform: scale(0.9);
                    opacity: 0.7;
                }

            }
        }
    }
    &--hide {
		transform: translate(0, -100%);
	}

	&--scrolled > .container > .header__nav > .header__menu li a{
		height: 4rem;
	}

}
