@import './vars.scss';

.about{
    z-index: 2;
    &__img{
        overflow: hidden;
        max-width: 18.4rem;
        margin: 0 auto;
        border-radius: 2rem;
        margin-top: 2rem;
        box-shadow: 0 4px 10px rgba(0,0,0,0.2);
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__info{
        .text{
            margin-top: 6rem;
            p{
                & + p{
                    margin-top: 3rem;
                }
            }
        }
    }
    &__header{
        padding: 3rem 0 2rem;
        @media (min-width: 640px) {
            padding: 5rem 0 2rem;
        }
        @media (max-width: 640px) {
            box-shadow: 0 4px 10px rgba(0,0,0,0.2);
            background-color: #2e344593;
        }
    }
    ul{
        li{
            & + li{
                margin-top: 3rem;
            }
        }
    }
    
}