.bttn{
    display: inline-flex;
    font-size: 2.4rem;
    padding: 1rem 2rem;
    border: 1px solid rgb(255, 255, 255);
    border-radius: .8rem;
    overflow: hidden;
    font-weight: 600;
    text-align: center;
    color: #fff;
    transition: all .3s ease;
    line-height: 150%;
    &:hover{
        transform: scale(1.1);
    }
    &:active{
        transform: scale(1);
    }
}


