
@import "scss/core";


@import "scss/header";
@import "scss/about";
@import "scss/projects";


#tsparticles{
    position: fixed;
}
.circle{
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    height: 40rem;
    background-color: #2e3445;
    z-index: 2;
    border-radius: 50%;
    box-shadow: 0 .4rem 1rem rgba(0,0,0,0.2);
    @media (max-width: 640px) {
        display: none;
    }
}




