.social{
    display: flex;
    align-items: center;
    margin-top: 3rem;
    justify-content: center;
    &__link{
        transition: all .2s linear;
        padding: 1.5rem;
        border-radius: .8rem;
        &:hover{
            transform: translateY(-10px) scale(1.1);
            box-shadow: 0 4px 10px rgba(0,0,0,0.2);
        }
        &:active{
            transform: scale(0.9);
        }
        img{
            pointer-events: none;
            width: 5rem;
            height: auto;
            transition: all .3s linear;
            opacity: 1;
        }
    }
}
