@import './vars.scss';


.projects{
    &__wrap{
        margin-top: 5rem;
        text-align: center;
        display: grid;
        grid-gap: 2rem;
        @media (min-width: 640px) {
            grid-gap: 0;
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__item{
        transition: all .2s linear;
        padding: 1rem;
        &:active{
            transform: scale(0.93);
        }
        &-img{
            background-color: rgba(128, 128, 128, 0.49);
            z-index: 1;
            min-height: 14rem;
            &::before, &::after{
                opacity: 0;
                transition: all .4s linear;
                content: '';
                position: absolute;
                z-index: 2;
            }
            &::before{
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: rgba(131, 131, 131, 0.4);
            }
            &::after{
                right: 2rem;
                top: 2rem;
                // background-image: url(../public/img/icons/link.png);
                background-image: url(../../public/img/icons/link.png);
                background-size: contain;
                background-repeat: no-repeat;
                width: 3rem;
                height: 3rem;
                z-index: 3;
            }
            img{
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
        h5{
            display: inline-block;
            color: $mainColor;
            font-size: 2.3rem;
            padding: 1rem 0 0 0.6rem;
            transition: all .3s linear;
            &::after{
                content: '';
                position: absolute;
                left: 50%;
                bottom: -1rem;
                height: 1px;
                width: 0;
                transform: translateX(-50%);
                background-color: $mainColor;
                transition: width .2s linear;

            }
        }
        &:hover > &-img{
            &::before, &::after{
                opacity: 1;
            }
        }
        &:hover{
            h5{
                &::after{
                    width: 100%;
                }
            }
        }
    }
    &__bttn-wrap{
        display: flex;
        justify-content: center;
    }
    &-bttn{
        margin-top: 2rem;
    }
    &__container{
        max-width: 90%;
        margin: 0 auto;
    }
}
